import { Component, ViewChild } from "@angular/core";
import { AlertService } from "../../../../../common/services/alert/alert.service";
import { SpinnerService } from "../../../../../common/services/spinner/spinner.service";
import { MasterRepo } from "../../../../../common/repositories";
import { Router, ActivatedRoute } from "@angular/router";
import { FormBuilder, FormGroup } from "@angular/forms";
import { SubLedgerMasterService } from "../subledger-master.service";
import { GenericPopUpComponent, GenericPopUpSettings } from "../../../../../common/popupLists/generic-grid/generic-popup-grid.component";

@Component({
    selector:'add-subledger',
    template: require('./add-subledger-master.component.html'),
    providers: [SubLedgerMasterService],
})

export class AddSubLedgerComponent{
    form: FormGroup;
    voucherTypeList:any[] = [];
    subLedgerMasterObj:any = <any>{};
    returnUrl :string;
    mode:string = 'New';
    ACID:string;

    @ViewChild("genericGridACList") genericGridACList: GenericPopUpComponent;
    gridACListPopupSettings: GenericPopUpSettings = new GenericPopUpSettings();
    
    constructor(   private alertService: AlertService,
        private loadingService: SpinnerService,
        protected masterService: MasterRepo,
        private router: Router,
        private _activatedRoute: ActivatedRoute,
        public _subLedgerMaster: SubLedgerMasterService,
        private fb: FormBuilder){
            // this.getVoucherTypeList()     
            try {
                this.router = router;
                if (!!this._activatedRoute.snapshot.params['returnUrl']) {
                    this.returnUrl = this._activatedRoute.snapshot.params['returnUrl'];
                }
                if (!!this._activatedRoute.snapshot.params['ACID']) {
                    let ACID: string = ""; let mode:string =""
                    ACID = this._activatedRoute.snapshot.params['ACID'];
                    mode = this._activatedRoute.snapshot.params['mode'];
                    this.mode=mode;
    ////console.log("@@this.mode",this.mode)
                    this._subLedgerMaster.getSubLedgerbyID(ACID)
                        .subscribe(data => {
                            if (data.status == 'ok') {
                                this.subLedgerMasterObj.SL_ACNAME=data.result?data.result[0].SL_ACNAME:"";
                                // this.mode = 'edit';
                                this.ACID = data.result?data.result[0].SL_ACID:"";  
                                this.subLedgerMasterObj.PAN_NO=  data.result?data.result[0].PAN_NO:"";
                                this.subLedgerMasterObj.HASMAINGROUP=  data.result?data.result[0].HASMAINGROUP:"";
                                this.subLedgerMasterObj.MainLedgerId=  data.result?data.result[0].MainLedgerId:"";
                                this.subLedgerMasterObj.MainLedgerName=  data.result?data.result[0].MainLedgerName:"";
                            }
                            else {
                                this.mode = '';
                            }
                        }, error => {
                            this.mode = '';
                            // this.modeTitle = "Edit2 -Error in CostCenter";
                            this.masterService.resolveError(error, "costCenterForm - getCostCenter");
                        }
                        )
                }
                else {
                    this.mode = "add";
                }    
            } catch (ex) {
                //console.log(ex);
                alert(ex);
            }       
        }





        getVoucherTypeList(){
            this.masterService.getAllVoucherList().subscribe(
                (res)=>{
                  if(res.status == 'ok'){
                    this.voucherTypeList = res.result;
                  }                                   
                }
            )
        }

        cancel(){
            this.returnUrl = "pages/account/AccountLedger/subledger-master"
            this.router.navigate([this.returnUrl]);
        }

        preventInput($event) {
            $event.preventDefault();
            return false;
          }

        onCheckMainGroup(value){
            console.log("maingroup checked", value);
            if(value == true){
                this.subLedgerMasterObj.HASMAINGROUP = 1;
            }else{
                this.subLedgerMasterObj.HASMAINGROUP = 0;
                this.subLedgerMasterObj.MainLedgerName = "";
                this.subLedgerMasterObj.MainLedgerId = '';
            }
        }

        onEnterLedger(){
            this.gridACListPopupSettings = {
              title: "Accounts",
              apiEndpoints: `/getSubLedgerACNAMEPageList`,
              defaultFilterIndex: 1,
              columns: [
                {
                  key: "ACID",
                  title: "AC CODE",
                  hidden: false,
                  noSearch: false
                },
                {
                  key: "ACNAME",
                  title: "A/C NAME",
                  hidden: false,
                  noSearch: false
                }
              ]
            };
      
            this.genericGridACList.show();
          }

          onAcSelect(acItem){
            if(acItem){
                this.subLedgerMasterObj.MainLedgerName = acItem.ACNAME;
                this.subLedgerMasterObj.MainLedgerId = acItem.ACID;
            }
          }

        onSave(){
            ////console.log("mdmd",this.mode,this.subLedgerMasterObj);
            if(this.subLedgerMasterObj.SL_ACNAME === "" || this.subLedgerMasterObj.SL_ACNAME === null || this.subLedgerMasterObj.SL_ACNAME === undefined){
                this.alertService.info("Please Enter Sub Ledger Name");
                return;
            }
            if(this.subLedgerMasterObj.SL_ACNAME.trim() === ""){
                this.alertService.info("Please Enter Sub Ledger Name");
                return;
            }
            if(this.subLedgerMasterObj.HASMAINGROUP == 1){
                if(this.subLedgerMasterObj.MainLedgerId == null || this.subLedgerMasterObj.MainLedgerId == undefined || this.subLedgerMasterObj.MainLedgerId == ''){
                    this.alertService.info("Please Select Main Ledger Name");
                    return;
                }
            }
            if(this.mode == "edit"){
                this.subLedgerMasterObj.SL_ACID=this.ACID;
            }
            this.loadingService.show("Data is Saving ..")
            this.masterService.saveSubLedgerMaster(this.mode, this.subLedgerMasterObj).subscribe(
                (response) =>{
                     if(response.status == 'ok'){
                        this.loadingService.hide();
                        // this.alertService.info(response.result);
                        this.alertService.info("Data Saved Successfully");
                        setTimeout(() => {
                            this.alertService.hide()
                          }, 1000);
                        this.cancel();
                     }else{
                        this.loadingService.hide();
                        this.alertService.info(response.result._body);
                     }
                },error =>{
                    this.loadingService.hide();
                }
            )
        }

        onDelete(){
            this.subLedgerMasterObj.SL_ACID=this.ACID;
            this.masterService.deleteSubLedgerMaster(this.mode, this.subLedgerMasterObj).subscribe(
                (response) =>{
                     if(response.status == 'ok'){
                        this.loadingService.hide();
                        this.alertService.info(response.result);
                        this.cancel();
                     }else{
                        // this.alertService.info(response.result);
                        this.alertService.info('Selected SubLedger is Already Used In Transaction');
                     
                     }
                },error =>{
                    this.loadingService.hide();
                    this.alertService.info(error.result._body);
                }
            )
        }
}


